import { useAIChatState } from './state';
import * as actions from './actions';

/**
 * @description AI聊天状态管理模块
 * @returns {Object} 状态和操作的组合对象
 */
export const useAIChatStore = () => {
  const state = useAIChatState();
  
  return {
    // 状态 - 保持与Vuex的getter名称一致以方便迁移
    get testResult() { return state.testResult; },
    get isTesting() { return state.isTesting; },
    get isLoading() { return state.isLoading; },
    get error() { return state.error; },
    
    // 操作
    ...actions
  };
}; 